/* createform.css */

.container-fluid {
  padding: 20px;
  background-color: #f8f9fa;
  font-family: Arial, sans-serif;
}

h4 {
  text-align: center;
  margin-bottom: 20px;
  color: #343a40;
}

.form-control,
.form-select {
  margin-bottom: 10px;
}

.question-card {
  border: 1px solid #ced4da;
  padding: 10px;
  border-radius: 4px;
  background-color: #fff;
  margin-bottom: 15px;
}

.question {
  font-weight: bold;
  margin-bottom: 10px;
}

.answer-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ced4da;
  border-radius: 4px;
}

.btn-sm {
  padding: 5px 10px;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}

.is-invalid {
  border-color: #dc3545;
}

.error-message {
  color: #dc3545;
  font-size: 0.875rem;
  margin-top: 5px;
}

.btn-success {
  background-color: #28a745;
  border-color: #28a745;
}

.btn-danger {
  background-color: #dc3545;
  border-color: #dc3545;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
}

.row {
  margin-bottom: 15px;
}
